<!-- 编辑知识点 -->
<template>
  <div class="edit-res">
    <div class="main">
      <div class="video-info">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item>我的资源</el-breadcrumb-item>
          <el-breadcrumb-item>编辑知识点</el-breadcrumb-item>
        </el-breadcrumb>
        <div class="title">{{resourceInfo.resourceName}}</div>
        <div class="desc">{{resourceInfo.subjectInfo}}</div>

        <div class="main-video-content-video">
          <div class="left">
            <div class="video-wrap">
              <video
                ref="videoSource"
                controls
                style="width:100%;height:100%;object-fit:cover;outline: none"
                :src="resourceInfo.filePath"
              ></video>
            </div>
            <div class="video-control">
              <div>
                <i class="iconfont icon-eyes"></i> {{resourceInfo.visitCount}}
              </div>
              <div>
                <i class="iconfont icon-arrow-1"></i> 下载
              </div>
              <!-- <div>
                <i class="iconfont icon-heart"></i> 收藏
              </div> -->
              <div>
                <i class="iconfont icon-share"></i> 分享
              </div>
              <!-- <div>
                <i class="iconfont icon-stars"></i> {{resourceInfo.avgScore}}
              </div> -->
            </div>
          </div>
          <div class="right">
            <div class="title">标记<span style="margin-left:10px" v-if="listNotes.length > 0">{{listNotes.length}}</span> </div>
            <el-scrollbar style="height:550px">
              <!-- 有知识点 -->
              <template v-if="listNotes.length > 0">
                <ul>
                  <li v-for="item in listNotes" :key="item.resourceNoteId">
                    <div class="content">
                      <p>{{item.resourceNoteContext}}</p>
                      <p>{{item.resourceNoteExplain}}</p>
                    </div>
                    <div class="control" @mouseleave="item.show=false" @mouseenter="item.show=true">
                      <i  class="iconfont icon-more p-center"></i>
                      <div v-if="item.show" class="control-btn">
                        <div @click="handleEdit(item)">
                          <i class="iconfont icon-edit p-center"></i>
                        </div>
                        <div @click="deleteResourceNoteInfo(item)">
                          <i class="iconfont icon-del p-center"></i>
                        </div>
                      </div>
                    </div>
                  </li>
                </ul>
              </template>
              <!-- 没有知识点 -->
              <template v-else>
                <div style="text-align:center;line-height:100px">目前暂时没有标记</div>
              </template>
            </el-scrollbar>
          </div>
        </div>
      </div>
      <div class="edit-info">
        <div class="title">编辑标记</div>
        <div class="item">
          <span>时间:</span>
          <div class="time-dot">
            {{tagTime | filterTime}}
            <span @click="handleSelectCurrentTime">选择时间点</span>
          </div>
        </div>
        <div class="item">
          <span>相关知识点:</span>
          <div class="know">
            <div class="add-btn" @click="handleAddKnowledge">+</div>
            <div class="lists">
              <div class="list-item" v-if="finnalSelect.pointsName">
                <p class="p2-overflow">{{finnalSelect.pointsName}}</p>
                <span>
                  <i @click="handleDeleteNotes" class="iconfont icon-close"></i>
                </span>
              </div>
            </div>
          </div>
        </div>
        <div class="item">
          <span>说明:</span>
          <div class="desc">
            <el-input
              type="textarea"
              :rows="2"
              placeholder="请输入内容"
              resize="none"
              v-model="textarea"
            ></el-input>
          </div>
        </div>
        <div class="sub-btn item">
          <span></span>
          <div @click="saveResourceNoteInfo">提交</div>
        </div>
      </div>
    </div>
    <res-footer></res-footer>

    <!-- modal 知识 knowledge-->
    <div class="add-knowledge-wrap">
      <el-scrollbar>
        <div class="add-knowledge-content">
          <div class="related">
            <div class="title">相关知识点</div>
            <div class="related-lists">
              <resKnowledgeBox :class="{active:selectNotes.id===item.id}" @click.native="handleSelectNote(item)" :info="item" v-for="item in relatedNotes" :key="item.id"></resKnowledgeBox>
            </div>
          </div>
          <div class="search">
            <div class="title">搜索知识点</div>
            <div class="search-box">
              <el-input placeholder="请输入内容" v-model="searchNoteValue">
                 <i @click="handleSearch" style="cursor:pointer" slot="suffix" class="el-input__icon el-icon-search"></i>
              </el-input>
            </div>
            <div class="search-lists">
              <resKnowledgeBox :class="{active:selectNotes.id===item.id}" @click.native="handleSelectNote(item)" :info="item" v-for="item in searchNotes" :key="item.id"></resKnowledgeBox>
            </div>
          </div>
        </div>
      </el-scrollbar>
    </div>
  </div>
</template>

<script>
import { formatSeconds } from "@/assets/javascript/ultis.js";
export default {
  props:['id'],
  components: {
    resFooter: () => import("@/components/res-footer"),
    resKnowledgeBox: () => import("@/components/res-knowledge")
  },
  data() {
    return {
      resourceNoteId:'',
      textarea: "",
      searchNoteValue: "",
      /* ----- */
      tagTime: "0",
      resourceInfo:{},
      listNotes:[],
      /* 相关知识点 */
      relatedNotes:[],
      /* 搜索知识点 */
      searchNotes:[],
      /* 选择知识点 */
      selectNotes:{
        id:'',
        pointsName:''
      },
      finnalSelect:{
        id:'',
        pointsName:''
      }
    };
  },
  created() {
	  console.log(this.id)
    this.getResourceDetail();
    this.handleSearch();
  },
  filters:{
    filterTime(time){
      return formatSeconds(time)
    }
  },
  methods: {
    /* 获取资源 */
    getResourceDetail(){
      this.$api.getResourceDetailByFile(this.id).then(res => {
        this.resourceInfo = res.data;
        this.selectRelevantCoreResourceNote();
        this.listNotesByResourceId();
      })
    },
    /* 获取相关知识点 */
    selectRelevantCoreResourceNote(){
      this.$api.selectRelevantCoreResourceNote(this.resourceInfo.resourceId).then(res => {
        this.relatedNotes = res.data;
      })
    },
    /* 获取旁边知识点 */
    listNotesByResourceId(){
      this.$api.listNotesByResourceId(this.resourceInfo.resourceId).then(res => {
        this.listNotes = res.data.map(item => Object.assign(item,{show:false}));
      })
    },
    /* 编辑知识点 */
    handleEdit(item){
      this.finnalSelect.pointsName = item.resourceNoteContext
      this.finnalSelect.id = item.resourceNoteId
      this.textarea = item.resourceNoteExplain
      this.resourceNoteId = item.resourceNoteId
      this.tagTime = item.resourceNoteTime
    },
    /* 删除知识点 */
    deleteResourceNoteInfo(item) {
      this.$myLayer.confirmLayer("确定删除当前知识点",() => {
        this.$api.deleteResourceNoteInfo(item.resourceNoteId).then(res => {
          this.listNotesByResourceId();
        })
      })
    },
    /* 搜索知识点 */
    handleSearch(){
      this.$api.selectCoreResourceNote(this.searchNoteValue).then(res => {
        this.searchNotes = res.data;
      })
    },
    handleSelectNote(item){
      this.selectNotes = {...item};
    },
    handleDeleteNotes(){
      this.selectNotes.id=''
      this.selectNotes.pointsName=''
      this.finnalSelect.id=''
      this.finnalSelect.pointsName=''
    },
    /* 增加知识点 */
    handleAddKnowledge() {
      this.$myLayer.formLayer("添加知识点", $(".add-knowledge-wrap"), [
        "960px",
        "577px"
      ],()=>{
        if(this.selectNotes.id) {
          this.finnalSelect = {...this.selectNotes};
        }
      });
    },
    reset(){
      this.resourceNoteId="";
      this.tagTime=0;
      this.textarea="";
      this.handleDeleteNotes();
    },
    saveResourceNoteInfo(){
      if(!this.finnalSelect.id) {
        return this.$myLayer.dangerLayer("请添加相关知识点");
      }
      const params = {
        context:this.finnalSelect.pointsName,
        explain:this.textarea,
        resourceId:this.resourceInfo.resourceId,
        time: this.tagTime
      }
      if(this.resourceNoteId) {
        /* 编辑 */
        params.resourceNoteId = this.resourceNoteId
        this.$api.updateResourceNoteInfo(params).then(res => {
          this.listNotesByResourceId();
          this.$myLayer.successLayer("更新成功");
        })
      } else {
        this.$api.saveResourceNoteInfo(params).then(res => {
          this.listNotesByResourceId();
           this.$myLayer.successLayer("添加成功")
        })
      }
      this.reset();
      
    },
    handleSelectCurrentTime() {
      let time = this.$refs.videoSource.currentTime;
      this.tagTime = time;
    }
  }
};
</script>

<style lang="scss" scoped>
.main-video-content-video {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  height: 650px;
  .left {
    flex: 1;
    .video-wrap {
      height: calc(100% - 65px);
    }
    .video-control {
      height: 65px;
      background: #202020;
      display: flex;
      align-items: center;
      & > div {
        margin-left: 30px;
      }
    }
  }
  .right {
    width: 320px;
    height: 650px;
    background: #141414;
    .title {
      height: 45px;
      line-height: 45px;
      padding-left: 15px;
      border-bottom: 1px solid #323232;
      color: #c7c7c7;
    }
    ul {
      padding: 15px;
      padding-top: 0;
      max-height: 700px;
      li {
        display: flex;
        padding: 10px 0;
        color: #c7c7c7;
        border-bottom: 1px solid #323232;
        position: relative;
      }
      li .content {
        flex: 1;
        p {
          &:first-child {
            color: #fff;
          }
          &:last-child{
            margin-top: 5px;
             color: #c7c7c7;
          }
        }
      }
      li .control {
        width: 30px;
        position: relative;
      }
      li .control > i {
        color: #7a7a7a;
        font-size: 20px;
      }
      li .control-btn {
        position: absolute;
        right: -15px;
        top: -10px;
        bottom: -10px;
        width: 100px;
        display: flex;
      }
      li .control-btn > div {
        position: relative;
        flex: 1;
        background: #292929;
      }
      li .control-btn > div:hover {
        background: #1f1f1f;
      }
      li .control-btn > div i {
        font-size: 20px;
        color: #2695ff;
        cursor: pointer;
      }
    }
  }
}
.edit-res {
  padding-top: 20px;
  overflow: hidden;
  background: #f3f5f7;
}
.main {
  width: 1200px;
  margin: 0 auto;
  .video-info > .title {
    color: #303133;
    font-size: 20px;
    margin: 10px 0;
  }
  .video-info > .desc {
    color: #606266;
    margin-bottom: 10px;
  }
}
.main {
  .edit-info {
    background: #fff;
    padding: 10px 20px 60px;
  }
  .edit-info .title {
    padding-bottom: 8px;
    color: #303133;
    font-size: 16px;
    border-bottom: 1px solid #e4e8ed;
  }
  .edit-info .item {
    display: flex;
    margin-top: 20px;
  }
  .edit-info .item > span {
    display: block;
    width: 80px;
    text-align: right;
    margin-right: 15px;
    flex-shrink: 0;
  }
  .edit-info .item .time-dot {
    width: 236px;
    height: 36px;
    border-radius: 4px;
    border: 1px solid #e5e5e5;
    color: #606165;
    line-height: 36px;
    padding-left: 14px;
    position: relative;
    > span {
      position: absolute;
      right: 0;
      top: 0;
      border-left: 1px solid #e5e5e5;
      text-align: center;
      cursor: pointer;
      width: 94px;
      height: 36px;
      color: #606165;
      line-height: 36px;
    }
  }
  .edit-info .item .know {
    .add-btn {
      border: 1px dashed #e5e5e5;
      background: #f8f9fa;
      border-radius: 4px;
      width: 72px;
      height: 36px;
      color: #999;
      cursor: pointer;
      font-size: 20px;
      text-align: center;
      line-height: 32px;
    }
    .lists {
      display: flex;
      flex-wrap: wrap;
    }
    .lists .list-item {
      width: 168px;
      height: 64px;
      background: #e7f1ff;
      border-radius: 4px;
      padding: 10px;
      position: relative;
      margin: 20px 20px 10px 0;
    }
    .lists .list-item p {
      color: #4a80f6;
    }
    .lists .list-item span {
      display: inline-block;
      width: 18px;
      height: 18px;
      background: #accdfb;
      line-height: 18px;
      text-align: center;
      color: #fff;
      border-radius: 50%;
      position: absolute;
      right: -8px;
      top: -8px;
      i {
        color: inherit;
        cursor: pointer;
      }
    }
  }
  .edit-info .item .desc {
    width: 640px;
  }
  .edit-info .sub-btn > div {
    width: 88px;
    height: 36px;
    background: rgba(38, 149, 255, 1);
    border-radius: 4px;
    cursor: pointer;
    color: #fff;
    line-height: 36px;
    text-align: center;
  }
}
.add-knowledge-wrap {
  display: none;
}
.add-knowledge-content {
  padding: 20px;
  .related .title {
    margin-bottom: 10px;
  }
  .related .related-lists {
    display: flex;
    flex-wrap: wrap;
  }

  .search .title {
    margin-top: 10px;
    margin-bottom: 16px;
  }
  .search .search-box {
    width: 280px;
    margin-bottom: 16px;
  }
  .search .search-lists {
    display: flex;
    flex-wrap: wrap;
  }
}
</style>